import React from 'react';

import { post, SERVER_ADDRESS } from '../../../utils/remote';
import { useDispatch } from 'react-redux';
import { createUserAction } from '../../../utils/redux/user';
import { useHistory } from 'react-router-dom';
import { makeStyles } from '@material-ui/styles';
import { Button, Card, CardContent, Typography } from '@material-ui/core';

export const endpoint = SERVER_ADDRESS + '/users/subscriptions';

const useStyles = makeStyles({
  card: {
    maxWidth: '300px',
    margin: '0 auto',
  },
  info: {
    margin: '1rem 0',
  },
  button: {
    margin: '1rem 0'
  }
})

export default function Reactivate(){
  const dispatch = useDispatch();
  const history = useHistory()
  const classes = useStyles();
  return <div>
    <Card className={classes.card}>
      <CardContent>
        <Typography align="center" variant="h6">Account Information</Typography>
        <Typography className={classes.info} variant="body2">Your account has been deactivated.</Typography>
        <Button
          className={classes.button}
          fullWidth
          variant="contained"
          color="primary"
          onClick={()=>{
            return post(endpoint, {
              body: {
                cancelled: false
              }
            }).then(({data: token})=>{
              dispatch(createUserAction(token));
              history.push('/');
            })
          }}
        >
          Reactivate
        </Button>
      </CardContent>
      
    </Card>
    
    
    
  </div>
}