import React from 'react';
import BarChart from '../../../../components/general/BarChart';

export default function DashboardBarChart({data, money}: any){
  return <BarChart barChartProps={{
    data,
    options: {
      maintainAspectRatio: false,
      scales: {
        yAxes: [{
            ticks: {
                beginAtZero: true,
                suggestedMax: 100,
                ...(!money ? {} : {
                    callback: function(value: number) {
                    return '$' + value;
                  }
                })
            }
        }]
      }
    }
  }} boxProps={{position: 'relative', m: '2rem auto', height: '300px', maxWidth: '600px'}}/>
}