import React, { cloneElement } from 'react';
import { Route, useHistory } from 'react-router-dom';
import { useSelector } from 'react-redux';
import { userSelector } from '../../../../utils/redux/user';
import AffiliateLanding from '../../../pages/affiliates/Landing';
import AffiliateLogin from '../../../pages/affiliates/Login';
import AffiliateSignup from '../../../pages/affiliates/Signup';
import AffiliateDashboard from '../../../pages/affiliates/Dashboard';
import AffiliateDisclaimer from '../../../pages/affiliates/Disclosure';
import { Box } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { Container, Divider, Typography } from '@material-ui/core';
import Top from '../../navigation/Top';

const useStyle = makeStyles({
  page: {
    paddingTop: '1rem',
    paddingBottom: '4rem',
    backgroundColor: '#eaf5e6',//grey[100],
    minHeight: '90vh',
  }
})

function PageContainer(props: any){
  const classes = useStyle();
  return <>
    <Top affiliate logo={'acts.Affiliates'}/>
    <Container maxWidth={false} className={classes.page}>
      {props.children}
    </Container>
  </>
}

export default function AffiliateRoutes() {
  return <>
    <Route exact path="/login"><PageContainer><AffiliateLogin /></PageContainer></Route>
    <Route exact path="/signup"><PageContainer><AffiliateSignup /></PageContainer></Route>
    <Route exact path="/"><RouteWrapper mustBeLoggedIn><PageContainer><AffiliateDashboard /></PageContainer></RouteWrapper></Route>
    <Route exact path="/overview"><AffiliateLanding /></Route>
  </>
};



function RouteWrapper({children, mustBeLoggedIn, ...props}: {children: JSX.Element, mustBeLoggedIn?: Boolean}){
  const history = useHistory();
  const user = useSelector(userSelector);
  if(mustBeLoggedIn){
    if(!user || user.type !== 'aff'){
      history.push('/overview')
      return null;
    }
  }  
  return <>    
    {
      cloneElement(children, props)
    }
  </>
}