import React from 'react';
import ButtonPrompt from '../../components/general/ButtonPrompt';
import { post, SERVER_ADDRESS } from '../../../utils/remote';
import { useDispatch, useSelector } from 'react-redux';
import { createUserAction, userSelector } from '../../../utils/redux/user';
import { useHistory } from 'react-router-dom';
import { uiCreditsSelector } from '../../../utils/redux/ui';
import { makeStyles } from '@material-ui/styles';
import { Button, TableBody, Typography } from '@material-ui/core';
import { Card, CardContent, Table, TableCell, TableHead, TableRow } from '@material-ui/core';
import { format, parseISO, addMonths } from 'date-fns';
import { sendEvent } from '../../../utils/ga';

export const endpoint = SERVER_ADDRESS + '/users/subscriptions';

const useStyles = makeStyles({
  card: {
    maxWidth: '300px',
    margin: '0rem auto 1rem',
  },
})

export default function Subscriptions(){
  const user = useSelector(userSelector);
  const credits = useSelector(uiCreditsSelector);
  const classes = useStyles();
  const dispatch = useDispatch();
  const history = useHistory()
  return <div>
    <Card className={classes.card}>
      <CardContent>
        <Typography align="center" variant="h6">
          Subscription Details
        </Typography>
      </CardContent>
    </Card>
    <Card className={classes.card}>
      <CardContent>
        <Table>
          <TableBody>
            <TableRow>
              <TableCell>
                Current Monthly Fee
              </TableCell>
              <TableCell>
                $2.00
              </TableCell>
            </TableRow>
            <TableRow>
              <TableCell>
                Credits Remaining
              </TableCell>
              <TableCell>
                {
                  credits.count
                }
              </TableCell>

            </TableRow>
<TableRow>
              <TableCell>
                Next Payment Due
              </TableCell>
              <TableCell>
                {
                  format(addMonths(parseISO(user.createdAt), credits.count+1), 'MMMM dd, yyyy')
                }
              </TableCell>
              
            </TableRow>
          </TableBody>
        </Table>
      </CardContent>
    </Card>
    <Card className={classes.card}>
      <CardContent>
        {
          user.cancelled ? <Button
            fullWidth
            variant="contained"
            color="primary"
            onClick={()=>{
              sendEvent({category: 'subscriptions', action: 'click', label: 'activate'});
              return post(endpoint, {
                body: {
                  cancelled: false
                }
              }).then(({data: token})=>{
                dispatch(createUserAction(token));
              })
            }}
            >
            Renew Subscription
            </Button> : <ButtonPrompt
            ga={{
              category: 'subscription',
              action: 'click',
              label: 'preconfirmCancel'
            }}
            raw
            buttonProps={{
              fullWidth: true,
              color: 'secondary'
            }}
            message={
              <>
                <Typography variant="body1">Are you sure you want to cancel your subscription?</Typography>
                <Typography variant="body2">You will lose access to the site at the end of your current billing cycle</Typography>
              </>
              
            }
            onConfirm={()=>{
              sendEvent({category: 'subscriptions', action: 'click', label: 'cancel'});
              return post(endpoint, {
                body: {
                  cancelled: true
                }
              }).then((res)=>{
                dispatch(createUserAction(res.data))
              })
            }}
          >
            Cancel Subscription
          </ButtonPrompt>
        }
      </CardContent>
    </Card>
  </div>
}

