import { Card, CardContent, Typography } from '@material-ui/core';
import React from 'react';
import { SERVER_ADDRESS, useCustomSwr } from '../../../../utils/remote';
import { Box } from '@material-ui/core';
import DashboardBarChart from './BarChart';

export default function AffiliateDashboard(){
  const { data: activeReferrals } = useCustomSwr(SERVER_ADDRESS+'/users/referrals')
  const { data: affiliateInformation } = useCustomSwr(SERVER_ADDRESS+'/users/affiliates')
  if(!affiliateInformation || !activeReferrals) return null
  const earningsData = {
    labels: Object.keys(affiliateInformation.history.earningsByDate),
    datasets: [{
      label: 'Earnings (USD) during bi-monthly period',
      data: Object.values(affiliateInformation.history.earningsByDate).map((v: any) => (v*0.1)/100),
    }]
  }
  const activeUsersData = {
    labels: Object.keys(affiliateInformation.history.earningsByDate).concat(['Today']),
    datasets: [{
      label: 'Numbers of active users durning bi-monthly period',
      data: Object.values(affiliateInformation.history.activeUsersByDate).concat([activeReferrals?.count]),
    }]
  }
  return <div>
    <Box m="1rem auto" maxWidth="600px">
      <Card>
        <CardContent>
          <Typography variant="h6" component="h2">Number of active referred accounts: <strong>{activeReferrals?.count}</strong></Typography>
        </CardContent>
      </Card>
    </Box>
    <Box m="1rem auto" maxWidth="600px">
      <Card>
        <CardContent>
          <DashboardBarChart data={earningsData} />
        </CardContent>
      </Card>
    </Box>
    <Box m="1rem auto" maxWidth="600px">
      <Card>
        <CardContent>
          <DashboardBarChart data={activeUsersData} />
        </CardContent>
      </Card>
    </Box>
  </div>
}