import React from 'react'
import {
    addActivity,
    removeActivity,
    removeActivityFromSchedule,
    addActivityToSchedule
} from '../utils';
import FAIcon from '../../../../components/general/FAIcon';
import { useSelector, useDispatch } from 'react-redux';
import { addedActivitySelector } from '../../../../../utils/redux/config';
import { IconButton } from '@material-ui/core';
import { sendEvent } from '../../../../../utils/ga';

type $props = any;

export default function AddActivityButtons({activity, classes, directToSchedule, disabled}: $props){
  const dispatch = useDispatch();
  const addedActivity = useSelector(addedActivitySelector(activity.id));
  if(directToSchedule){
    return addedActivity?.inSchedule ? <IconButton classes={classes} onClick={()=>{
      if(!disabled){
        sendEvent({category: 'schedule', action: 'click', label: 'remove'});
        dispatch(removeActivityFromSchedule(activity));
      }
    }} color="secondary">
      <FAIcon type="calendar-minus" />
    </IconButton> : 
    <IconButton classes={classes} onClick={()=>{
      if(!disabled){
        sendEvent({category: 'schedule', action: 'click', label: 'add'});
        dispatch(addActivityToSchedule(activity));
      }
    }} color="primary"><FAIcon type="calendar-plus" /></IconButton>
  }
  return addedActivity ? <IconButton classes={classes} onClick={()=>{
    if(!disabled) {
      sendEvent({category: 'bookmarks', action: 'click', label: 'remove'});
      dispatch(removeActivity(activity))
    }
  }} color="secondary">
    <FAIcon type="bookmark" />
  </IconButton> : 
  <IconButton classes={classes} onClick={()=>{
    if(!disabled){
      sendEvent({category: 'bookmarks', action: 'click', label: 'add'}); 
      dispatch(addActivity(activity))
    }
  }} color="primary"><FAIcon type="bookmark" /></IconButton>
}