import React from 'react';
import { useHistory } from 'react-router-dom';
import {UserCreateCard} from '../../../users/Create';
import { SERVER_ADDRESS } from '../../../../utils/remote';

export const endpoint = SERVER_ADDRESS+'/users/affiliates';

export default function AffiliateSignup(){
  const history = useHistory();
  return <UserCreateCard
    api={endpoint}
    onSuccessCB={()=>{
      history.push('/')
    }}
    type="aff"
  />
}