import React, { useEffect, useState } from 'react';
import { SERVER_ADDRESS, useCustomSwr } from '../../../../../utils/remote';
import { makeStyles } from '@material-ui/styles';
import { ListItemIcon, MenuItem, Typography } from '@material-ui/core';
import { Chip } from '@material-ui/core';
import { Input } from '@material-ui/core';
import { Select } from '@material-ui/core';
import { InputLabel } from '@material-ui/core';
import { FormControl } from '@material-ui/core';
import { Divider, TextField } from '@material-ui/core';
import FAIcon from '../../../../components/general/FAIcon';


export default function DashboardTabForm({ordinal, dashboardTab, debouncedOnChange, onChange}: any){
  return <>
    <TextField label="Tab Name" placeholder={`Tab ${ordinal+1}`} defaultValue={dashboardTab.name} onChange={(event)=>{
      debouncedOnChange(event.target.value)
    }} />
    <Divider variant="inset"/>
    <PickAges values={dashboardTab.ages} onSave={(nextAges: string[])=>onChange('ages', nextAges)} color={!dashboardTab.ages.length ? 'secondary' : undefined} />
    <Divider variant="inset"/>
    <PickSubjects values={dashboardTab.subjects} onSave={(nextSubjects: string[])=>onChange('subjects', nextSubjects)} color={!dashboardTab.subjects.length ? 'secondary' : undefined} />
    <Divider variant="inset"/>
  </>
}

const useStyles = makeStyles((theme) => ({
  formControl: {
    // margin: theme.spacing(1),
    minWidth: 250,
    maxWidth: 500,
  },
  chips: {
    display: 'flex',
    flexWrap: 'wrap',
  },
  chip: {
    margin: 2,
  },
  noLabel: {
    // marginTop: theme.spacing(3),
  },
}));

const ITEM_HEIGHT = 48;
const ITEM_PADDING_TOP = 8;

const MenuProps = {
  PaperProps: {
    style: {
      maxHeight: ITEM_HEIGHT * 4.5 + ITEM_PADDING_TOP,
      minWidth: 250,
    },
  },
};

function PickAges(props: any){
  const [orderedAgeTags, setOrderedAgeTags] = useState<any[]>([]);

  const { data } = useCustomSwr(SERVER_ADDRESS+'/tags', {
    deps: [],
    defaultValue: [],
    cache: true,
  })
  useEffect(()=>{
    const ages = data.filter((t: any)=>t.type==='age');
    const months = ages.filter((t:any)=>t.name.indexOf('month') > -1)
    const years = ages.filter((t:any)=>t.name.indexOf('month') === -1)
    setOrderedAgeTags(months.concat(years));
  }, [data])
  return <SelectOptions
    title="Select Ages"
    {...props}
    id="select-ages"
    tags={orderedAgeTags}
  />
}

function PickSubjects(props: any){
  

  const { data } = useCustomSwr(SERVER_ADDRESS+'/tags', {
    deps: [],
    defaultValue: [],
    cache: true,
  })
  
  return <SelectOptions
    title="Select Subjects"
    {...props}
    id="select-subjects"
    tags={data.filter((t:any)=>t.type==="subject")}
  />
}

function SelectOptions({values, onSave, color, tags, id, title}: any){
  const handleChange = (event: React.ChangeEvent<{ value: unknown }>) => {
    onSave(event.target.value)
  };
  const classes = useStyles();
  return <FormControl className={classes.formControl}>
    <InputLabel id={`${id}-label`}><Typography color={color}>{title}</Typography></InputLabel>
    <Select
      labelId={`${id}-label`}
      id={id}
      multiple
      value={values}
      onChange={handleChange}
      input={<Input id={`input-${id}`} />}
      renderValue={() => (
        <div className={classes.chips}>
          {values.map((name: any) => (
            <Chip key={name} label={'#'+name} className={classes.chip} />
          ))}
        </div>
      )}
      MenuProps={MenuProps}
    >
      {tags.map((tag: any) => (
        <MenuItem key={tag.name} value={tag.name} //style={getStyles(name, personName, theme)}
        >
          <ListItemIcon>
            <span>
              <FAIcon raw={tag.icon} />
              <span>{tag.side_content}</span>
            </span>
          </ListItemIcon>
          {
            tag.description
          }
        </MenuItem>
      ))}
    </Select>
  </FormControl>
}