import qs from 'query-string';
import { useEffect, useState } from 'react';
import { mergeWith, isArray } from 'lodash';
import { dispatch } from './redux';
import { getToken } from './auth';
import lscache from 'lscache';
import { createUserAction } from './redux/user';

export const SERVER_ADDRESS = process.env.REACT_APP_SERVER_ADDRESS || "";
const genDefaultHeaders = ()=>({
  Authorization: getToken() ? `Bearer ${getToken()}` : '',
  'Content-Type': 'application/json',
});

// lscache.flush();
const post = (url: string, options:any={headers: {}})=>fetch(url, {
  method: 'POST',
  headers: {
    ...genDefaultHeaders(),
    ...options.headers,
  },
  ...options,
  body: JSON.stringify(options.body),
}).then(handleErrors);

const put = (url: string, options: any={headers: {}})=>fetch(url, {
  method: 'PUT',
  headers: {
    ...genDefaultHeaders(),
    ...options.headers,
  },
  ...options,
  body: JSON.stringify(options.body),
}).then(handleErrors);

const del = (url: string, options: any={headers: {}})=>fetch(url, {
  method: 'DELETE',
  headers: {
    ...genDefaultHeaders(),
    ...options.headers,
  },
  ...options,
}).then(handleErrors);

const get = (url: string, options: any={headers: {}})=>{
  const {headers, ...otherOptions} = options;
  return fetch(url, {
    method: 'GET',
    headers: {
      ...genDefaultHeaders(),
      ...headers,
    },
    ...otherOptions,
  }).then(handleErrors);
}

const handleErrors = (res: any)=>{
  if(res.status === 401){
    dispatch(createUserAction(null));
  }
  if(res.status >= 400){
    throw res
  }
  return res.json();
}

const customMerge = function (objValue: any, srcValue: any) {
  if (isArray(objValue)){
    return objValue;
  }
  if(objValue !== undefined){
    return objValue
  }
}

const useCustomSwr = (apiKey: string, options:any={})=>{
  const [ page, setPage ] = useState(0);
  const [ data, setData ] = useState<any>(options.defaultValue || null);
  const [ noMoreContent, setNoMoreContent ] = useState(false);
  const incrementPage = ()=>setPage(page+1);
  const resetPage = ()=>setPage(0);
  
  useEffect(()=>{
    if(!options.doNotFire){
      let suffix='';
      if(options.query){
        suffix=qs.stringify({
          ...options.query,
          ...(options.page ? {
            page
          } : {}),
        }, {arrayFormat: 'bracket'})
      }
      const url = apiKey+(suffix ? '?' + suffix : suffix);
      let dataRequest
      if(options.cache && lscache.get(url)){
        dataRequest = Promise.resolve(JSON.parse(lscache.get(url)));
      } else {
        dataRequest = get(url, options.requestOptions)
        .then((json)=>{
          if(options.cache){
            lscache.set(url, JSON.stringify(json), 10);
          }
          return json;
        });
      }
      dataRequest.then((res)=>{
        if(page){
          if(!res.length){
            setNoMoreContent(true)
          }
          return setData(data.concat(res))
        }
        return setData(res);
      });
    }
  }, (options.deps || []).concat([page]));
  const mutate = (nextData: any)=>{
    const mergeResults = mergeWith({...nextData}, data, customMerge);
    setData(mergeResults);
  }
  return { data, mutate, incrementPage, resetPage, page, noMoreContent }
}

export {
  get,
  post,
  put,
  del,
  useCustomSwr,
}