import React, { useEffect, useState } from 'react';
import Typography from '@material-ui/core/Typography';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import { v4 } from 'uuid';
import * as yup from 'yup';
import { useForm } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import { useLocation } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/styles';
import { Card, CardActions, CardContent, CardHeader, Divider } from '@material-ui/core';
import qs from 'query-string';
import { Box } from '@material-ui/core';
import { Link } from 'react-router-dom';
import { SERVER_ADDRESS, post } from '../../../utils/remote';
import { setToken } from '../../../utils/auth';

import { createUserAction } from '../../../utils/redux/user';

import { setupIntents } from '../../../utils/stripe';
import { FormHelperText } from '@material-ui/core';
import { Checkbox } from '@material-ui/core';
import { FormControlLabel } from '@material-ui/core';

export const endpoint = SERVER_ADDRESS+'/privateUsers';

const useStyles = makeStyles({
  root: {
    maxWidth: '300px',
    margin: '0 auto',
  },
})

export default function UserCreate(){
  const location = useLocation();
  const parsed = qs.parse(location.search);
  return <UserCreateCard 
    onSuccessCB={()=>setupIntents()}
    referralCode={(parsed.referral_code as string) || ''}
    api={endpoint}
    additionalInformation={({validReferralCode}: {validReferralCode: string})=><CardContent>
      {
          <Typography variant="body1"><Box fontWeight="fontWeightBold" m="1">{`${validReferralCode ? 'First two months are free' : 'First month is free'}`}</Box></Typography>
      }
      <Divider/>
      <Typography variant="body2">Monthly price is $2.00(USD)/month</Typography>
      <Typography variant="body2">Cancel at anytime</Typography>
    </CardContent>}
  />
}

export function UserCreateCard(props: any) {
  const classes = useStyles();
  return (
    <div>
      <Card className={classes.root}>
        <CardHeader title="Signup"/>
          <UserCreateForm
            {...props}
          />
      </Card>
    </div>
  );
}

const schema = yup.object().shape({
  // username: yup.string().matches(/^[a-z0-9]+$/i, 'Only alphanumeric characters').required(),
  termsofuse: yup.bool().oneOf([true], 'You must accept our Terms of Use').required(),
  email: yup.string().email().required(),
  password: yup.string().min(6).max(122).required(),
  referralCode: yup.string().optional(),
});



const UserCreateForm = ({referralCode, api, onSuccessCB, additionalInformation}: {additionalInformation?: (a: any)=>JSX.Element, referralCode?: string, api: string, onSuccessCB: Function})=>{
  const { handleSubmit, register, errors } = useForm({
    resolver: yupResolver(schema),
  });
  const [apiErrors, setApiErrors] = useState<string>("");
  const [referralCodeError, setReferralCodeError] = useState<string>("");
  const dispatch = useDispatch();
  useEffect(()=>{
    if(referralCode){
      yup.string().uuid()
      .isValid(referralCode).then((valid)=>{
        if(!valid){
          setReferralCodeError("Bad referral code. You may continue to signup, but the referral will not be counted towards possible credits")
        }
      })
    }
  }, [])
  const validReferralCode = referralCodeError ? "" : (referralCode || '');
  const onSubmit = async (data: any) => {    
    post(api, {
      body:
        {
          id: v4(),
          // username: data.username,
          email: data.email,
          password: data.password,
          referralCode: data.referralCode
        }
    }).then(async (res)=>{
      setToken(res.data);
      dispatch(createUserAction(res.data));
    }).then( async ()=>{
      onSuccessCB()
    })
    .catch((error)=>{
      error.json().then((err: any) => {
        setApiErrors(err.message);
      });
    });
  }
  const errorExists = referralCodeError || apiErrors;
  return <form
      onSubmit={handleSubmit(onSubmit)}
    >
    <CardContent>
      {
        !errorExists ? null : <Typography variant="body2" color="secondary">{errorExists}</Typography>
      }
      <TextField
        id="email"
        name="email"
        inputRef={register}
        label="Email"
        fullWidth
        error={!!errors.email}
        helperText={errors.email?.message}
      />
      <TextField 
        fullWidth
        inputRef={register}
        id="password"
        name="password"
        label="Password"
        type="password"
        error={!!errors.password}
        helperText={errors.password?.message}
      />
      <FormControlLabel
        control={<Checkbox inputRef={register} name="termsofuse" />}
        label="Accept our Terms of Use"
      />
      {
        !errors.termsofuse ? null : <FormHelperText><Typography variant="body2" color="secondary">{errors.termsofuse?.message}</Typography></FormHelperText>
      }
      <FormHelperText><Link to="/termsofuse">Terms of Use</Link></FormHelperText>
      <input ref={register} name="referralCode" hidden value={validReferralCode} />
    </CardContent>
    {
      additionalInformation ? additionalInformation({validReferralCode}) : null
    }
    <CardActions>
      <Button type="submit">Signup</Button>
    </CardActions>
  </form>
}