import React from 'react';
import { makeStyles } from '@material-ui/core';
import SheduleSlot from './Slot';
import PortraitSlot from './PortraitSlot';
import SquareSlot from './SquareSlot';
import Scroll from '../Scroll';;

type $props = {
  wrap?: Boolean,
  addedActivityPage?: Boolean,
  data: any[],
  featuredPortrait?: Boolean,
  featuredSquare?: Boolean,
  preview?: Boolean,
  renderSlots?: Function,
}
const useStyles = makeStyles({
  root: (props: {wrap: Boolean}) => ({
    marginTop: '1rem',
    whiteSpace: props.wrap ? 'normal' : 'nowrap',
    // whiteSpace: 'normal',
    overflowX: 'scroll',
  }),
});

function SheduleRow({ addedActivityPage, wrap, data, featuredPortrait, featuredSquare, preview, renderSlots }: $props){  
  const classes = useStyles({wrap: !!(addedActivityPage || wrap)});
  
  const Slot = featuredPortrait ? PortraitSlot : featuredSquare ? SquareSlot : SheduleSlot
  const slideWidth = featuredPortrait ? 170 : featuredSquare ? 400 : 280;
  const slots = data.map((activity, i)=>{
    return <Slot key={activity.id} preview={preview} addedActivityPage={addedActivityPage}  shedule={activity} className="swiper-slide" />
  });
  // const slots = [<div>Slide 1</div>, <div>Slide 2</div>]
  if(renderSlots) return renderSlots(slots)
  if(wrap || addedActivityPage) return <div className={classes.root}>
    {
      slots
    }
  </div>
  return <Scroll slideWidth={slideWidth} renderSlides={()=>slots} />
  }

export default SheduleRow