import { Button, Divider, Grid, Typography } from '@material-ui/core';
import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Box } from '@material-ui/core';
import copy from 'copy-to-clipboard';
import { userSelector } from '../../../../../utils/redux/user';
import FAIcon from '../../../general/FAIcon';
import MyPopover from '../../../general/Popover';
import { Tooltip } from '@material-ui/core';
import { createUserAction } from '../../../../../utils/redux/user';
import { uiCreditsSelector, createUIAction } from '../../../../../utils/redux/ui';
import { makeStyles } from '@material-ui/styles';
import { sendEvent } from '../../../../../utils/ga';

const useStyles = makeStyles((theme: any)=>({
  icon: {
    cursor: 'pointer',
    color: theme.palette.primary.main
  }
}))

interface $props {
  affiliate?: Boolean
}

export default function TopNavigationRefer({affiliate}: $props){
  const [copied, setCopied] = React.useState(false);
  const classes = useStyles();
  const user = useSelector(userSelector);
  const credits = useSelector(uiCreditsSelector);
  const dispatch = useDispatch();
  const code = process.env.REACT_APP_CLIENT_URL + `/#/signup?referral_code=${user.id}`;
  return <MyPopover
    ga={{
      category: 'refer',
      action: 'click',
      label: affiliate ? '20percent' : '1FreeMonth'
    }}
    disableHover
    popoverProps={{onClose: ()=>{
      setCopied(false)
      dispatch(createUIAction({
        credits: {
          ...credits,
          hasNew: false
        }
      }))
    }}}
  renderHoverableContent={()=><Tooltip enterDelay={0} title="Referral Info"><Button variant="contained" color={credits.hasNew ? "secondary" : "primary"}>{affiliate ? 'Earn 20%' : '+1 Free Month'}</Button></Tooltip>}
    renderOnHoverContent={()=><Box width="20rem" whiteSpace="normal" padding="1.5rem">
      {
      affiliate ? <>
        <Typography variant="body2">Earn <strong>20%</strong> of revenue generated from users who sign up with this referral link.</Typography>
        <Box m="1rem"><Divider /></Box>
        <Typography variant="body2">Users who sign up with this link will start with <strong>two free months</strong>.</Typography>
        <Box m="1rem"><Divider /></Box>
        <Typography variant="body2">You can only earn revenue from a user for the first five years of their membership, and your total earnings through this program are capped at <strong>$2,000,000.00 (USD)</strong></Typography>
      </>
      : 
      <Typography variant="body2">
          Send this referral link to a friend and you both get an additional <strong>free</strong> month when they sign up'
      </Typography>
      }
      <Box m="1rem"><Divider /></Box>
      <Box>
        <Grid container>
          <Grid item xs={3}>
            {
              copied ? "Link copied to clipboard!" : <Tooltip title="copy to clipboard">
              <span className={classes.icon} onClick={()=>{
                sendEvent({category: 'reffer', action: 'click', label: 'copy'});
                copy(code)
                setCopied(true)
              }}>
                <FAIcon 
                type="clipboard" size="2x" />
                <Typography>Copy Link</Typography>
              </span>
            </Tooltip>
            }
          </Grid>
          
          <Grid item xs={9}>
            <Typography variant="body2">{code}</Typography>
          </Grid>
        </Grid>
        {
          credits.count === 0 ? null : <Box m="1rem 0">
            <Divider />
          </Box>
        }
        {
          credits.count === 0 ? null : <Grid container><Grid item>
            <Typography variant="body2"><strong>Congratulations</strong><>, you have</><strong>{credits.count}</strong><>{` free month credit${credits.count > 1 ? 's' : ''}`}</>
            </Typography>
          </Grid></Grid>
        }
      </Box>
    </Box>
    }
  />
}