import React from 'react';
import { v4 } from 'uuid';
import { MoreVert } from '@material-ui/icons';
import { Menu } from '@material-ui/core';
import { IconButton } from '@material-ui/core';
import { $sendEventProps, sendEvent } from '../../../../utils/ga';

type $props = {
  renderIcon?: any,
  renderMenuItems: (arg: Function)=>any,
  buttonComp?: any,
  buttonProps?: any,
  id?: string,
  ga?: $sendEventProps,
}

export default function IconMenu(props: $props) {
  const [anchorEl, setAnchorEl] = React.useState<HTMLElement|null>(null);
  const [id] = React.useState(v4())

  const handleClick = (event: any) => {
    if(props.ga){
      sendEvent(props.ga)
    }
    setAnchorEl(event.currentTarget);
  };

  const handleClose = () => {
    setAnchorEl(null);
  };
  const Button = props.buttonComp || IconButton;
  return (
    <div>
      <Button aria-controls="menu" aria-haspopup="true" onClick={handleClick} {...(props.buttonProps || {})}>
        {props.renderIcon || <MoreVert/> }
      </Button>
      <Menu
        id={props.id || id}
        anchorEl={anchorEl}
        keepMounted
        open={Boolean(anchorEl)}
        onClose={handleClose}
      >
      {
        props.renderMenuItems(handleClose)
      }
      </Menu>
    </div>
  );
}