import React from 'react';
import { Popover } from '@material-ui/core';
import { debounce } from 'lodash';
import { $sendEventProps, sendEvent } from '../../../../utils/ga';

type $props = {
  disableHover?: Boolean,
  disablePopup?: Boolean,
  className?: string,
  renderHoverableContent: Function,
  renderOnHoverContent: Function,
  popoverProps?: any,
  popoverClasses?: Object,
  ga?: $sendEventProps
}

export default function MyPopover({ga, disablePopup, disableHover, className, popoverClasses, renderHoverableContent, renderOnHoverContent, popoverProps={}} : $props){
  const [mouseStopped, setMouseStopped] = React.useState(false);
  const [anchorEl, setAnchorEl] = React.useState(null);
  const [candidateAnchorEl, setCandidateAnchorEl] = React.useState(null);
  const debouncedSetMouseStopped = debounce(()=>setMouseStopped(true), 500);
  React.useEffect(()=>{
    if(candidateAnchorEl && mouseStopped){
      if(ga){
        sendEvent({
          ...ga,
          action: ga.action+':hover'
        });
      }
      setAnchorEl(candidateAnchorEl);
    }
  }, [candidateAnchorEl, mouseStopped])
  
  const handleHide = ()=>{
    setCandidateAnchorEl(null);
    setAnchorEl(null);
    setMouseStopped(false)
  }
  const hoverableContentProps = {
    onClick(event: any){
      if(ga){
        sendEvent(ga);
      }
      setAnchorEl(event.currentTarget)
    },
    onMouseEnter(event: any){
      if(!disableHover) setCandidateAnchorEl(event.currentTarget)
    },
    onMouseLeave(event: any){
      if(!disableHover) setCandidateAnchorEl(null)
    },
    onMouseMove(){
      if(!disableHover) debouncedSetMouseStopped()
    },
  }
  if(disablePopup)
    return <div className={className}>{renderHoverableContent()}</div>
  return <><div className={className}
        // onMouseLeave={handleHide}
        {...hoverableContentProps}
    >
      {
        renderHoverableContent()
      }
    </div>
    <Popover
      classes={popoverClasses}
      open={Boolean(anchorEl)}
      anchorEl={anchorEl}
      {...popoverProps}
      onClose={()=>{
        handleHide()
        if(popoverProps.onClose) popoverProps.onClose();
      }}
      disableRestoreFocus
    >
      <div
      // onMouseLeave={handleHide}
      >
        {
          renderOnHoverContent()
        }
      </div>
    </Popover>
  </>
}

