const ga = ()=>(window as any).ga;
export default ga
export const sendEvent = (gaProps: $sendEventProps)=>{
  ga()('send', 'event', gaProps.category, gaProps.action, gaProps.label, gaProps.value)
};
export const sendPageView = (location: string)=>{
  ga()('send', 'pageview', location);
}

export type $sendEventProps = {
  category: string,
  action: string,
  label: string,
  value?: number
}