import React, {useState} from 'react';
import { Link } from 'react-router-dom';
import Menu from '@material-ui/core/Menu'
import MenuItem from '@material-ui/core/MenuItem'
import IconButton from '@material-ui/core/IconButton';
import AccountCircle from '@material-ui/icons/AccountCircle';
import { useHistory } from 'react-router-dom';
import { useDispatch } from 'react-redux';
import { makeStyles } from '@material-ui/styles';
import { Tooltip } from '@material-ui/core';
import { sendEvent } from '../../../../../utils/ga';

const useStyles = makeStyles({
  link: {
    textDecoration: 'none',
    color: 'inherit'
  }
})

export default function Profile({className}: {className: string}){
  const classes = useStyles();
  const [anchorEl, setAnchorEl] = useState<HTMLElement|null>(null);
  const dispatch = useDispatch();
  const history = useHistory();
  const handleClose = ()=>{
    setAnchorEl(null);
  }
  const handleClick = (event: any) => {
    sendEvent({
      category: 'account',
      action: 'click',
      label: 'none'
    })
    setAnchorEl(event.currentTarget);
  };
  return <>
    <Tooltip enterDelay={0} title="Account">
      <IconButton className={className} color="inherit" onClick={handleClick} aria-label="open profile menu" component="span">
        <AccountCircle />
      </IconButton>
    </Tooltip>
    <Menu
      id="profile-menu"
      anchorEl={anchorEl}
      keepMounted
      open={Boolean(anchorEl)}
      onClose={handleClose}
    > 
      <MenuItem>
        <Link className={classes.link} to="/subscriptions">Subscriptions</Link>
      </MenuItem>
      <MenuItem onClick={()=>{
        sendEvent({
          category: 'account',
          action: 'click',
          label: 'logout'
        })
        handleClose()
        dispatch({type: 'RESET'})
        history.push('/')
      }}>Logout</MenuItem>
    </Menu>
  </>
}